import Modal from 'flarum/common/components/Modal';
import Button from 'flarum/common/components/Button';
import Stream from 'flarum/common/utils/Stream';
import app from 'flarum/admin/app';

export default class CreateRedemptionModal extends Modal {
  oninit(vnode) {
    super.oninit(vnode);

    // Ë°®ÂçïÊï∞ÊçÆ
    this.groupId = Stream('');
    this.days = Stream('');
    this.amount = Stream('1');

    // Áä∂ÊÄÅÊéßÂà∂
    this.loading = false;
    this.generatedCodes = null; // Â≠òÂÇ®ÁîüÊàêÂêéÁöÑÁªìÊûú
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return this.generatedCodes ? 'ÁîüÊàêÊàêÂäü' : 'ÁîüÊàêÂÖëÊç¢Á†Å';
  }

  content() {
    // === Âú∫ÊôØ 2ÔºöÁîüÊàêÊàêÂäüÔºåÊòæÁ§∫ÂØºÂá∫ÁïåÈù¢ ===
    if (this.generatedCodes) {
        const codesText = this.generatedCodes.join('\n');

        return m('.Modal-body', [
            m('.Form-group', [
                m('p', {style: 'color: #109e59; font-weight: bold; text-align: center; margin-bottom: 15px;'},
                    `üéâ ÊàêÂäüÁîüÊàê ${this.generatedCodes.length} ‰∏™ÂÖëÊç¢Á†ÅÔºÅ`
                ),
                m('textarea.FormControl', {
                    style: 'height: 200px; font-family: monospace; font-size: 13px; line-height: 1.5;',
                    readonly: true,
                    onclick: (e) => e.target.select() // ÁÇπÂáªËá™Âä®ÂÖ®ÈÄâ
                }, codesText)
            ]),

            m('.Form-group', { style: 'display: flex; gap: 10px;' }, [
                // Â§çÂà∂ÊåâÈíÆ
                m(Button, {
                    className: 'Button Button--primary Button--block',
                    onclick: () => {
                        navigator.clipboard.writeText(codesText);
                        app.alerts.show({type: 'success'}, 'Â∑≤Â§çÂà∂ÊâÄÊúâÂÖëÊç¢Á†Å');
                    }
                }, '‰∏ÄÈîÆÂ§çÂà∂'),

                // ‰∏ãËΩΩÊåâÈíÆ
                m(Button, {
                    className: 'Button Button--block',
                    onclick: () => this.downloadTxt(codesText)
                }, '‰∏ãËΩΩ TXT'),
            ]),

            // ÂÖ≥Èó≠ÊåâÈíÆÔºàÁîüÊàêÂÆå‰∫ÜÔºåÂÖ≥Èó≠Êó∂Âà∑Êñ∞ÂàóË°®Ôºâ
            m('.Form-group', [
                m(Button, {
                    className: 'Button Button--link Button--block',
                    onclick: () => this.closeAndRefresh()
                }, 'ÂÆåÊàêÂπ∂ÂÖ≥Èó≠')
            ])
        ]);
    }

    // === Âú∫ÊôØ 1ÔºöÂ°´ÂÜôË°®Âçï ===
    return m('.Modal-body', [
      m('.Form', [
        m('.Form-group', [
          m('label', 'Áæ§ÁªÑ ID'),
          m('input.FormControl', { bidi: this.groupId, placeholder: '‰æãÂ¶Ç: 7' })
        ]),
        m('.Form-group', [
          m('label', 'ÊúâÊïàÂ§©Êï∞'),
          m('input.FormControl', { bidi: this.days, placeholder: '‰æãÂ¶Ç: 30' })
        ]),
        m('.Form-group', [
          m('label', 'ÁîüÊàêÊï∞Èáè'),
          m('input.FormControl', { bidi: this.amount, type: 'number', placeholder: '1' })
        ]),
        m('.Form-group', [
          m(Button, {
            className: 'Button Button--primary Button--block',
            loading: this.loading,
            onclick: this.onsubmit.bind(this)
          }, 'Êèê‰∫§ÁîüÊàê')
        ])
      ])
    ]);
  }

  // ËæÖÂä©ÂáΩÊï∞Ôºö‰∏ãËΩΩ TXT Êñá‰ª∂
  downloadTxt(content) {
    const blob = new Blob([content], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `redemption_codes_${new Date().toISOString().slice(0,10)}.txt`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }

  // ÂÖ≥Èó≠Âπ∂Ëß¶ÂèëÂà∑Êñ∞
  closeAndRefresh() {
      this.hide();
      if (this.attrs.onSuccess) this.attrs.onSuccess();
  }

  onsubmit(e) {
    e.preventDefault();
    this.loading = true;

    app.request({
      method: 'POST',
      url: app.forum.attribute('apiUrl') + '/redemption/create',
      body: {
        group_id: this.groupId(),
        days: this.days(),
        amount: this.amount()
      }
    }).then((response) => {
      this.loading = false;
      // ÂÖ≥ÈîÆÔºö‰∏çÂÖ≥Èó≠ÂºπÁ™óÔºåËÄåÊòØÂ∞ÜËøîÂõûÁöÑ codes Â≠òÂÖ•Áä∂ÊÄÅÔºåËß¶ÂèëËßÜÂõæÊõ¥Êñ∞
      if (response.codes) {
          this.generatedCodes = response.codes;
          m.redraw();
      } else {
          // Â¶ÇÊûúÂêéÁ´ØÊ≤°ËøîÂõû codes (ÂÖºÂÆπÊóß‰ª£Á†Å)ÔºåÂàôÁõ¥Êé•ÂÖ≥Èó≠
          this.hide();
          app.alerts.show({ type: 'success' }, 'ÁîüÊàêÊàêÂäü');
          if (this.attrs.onSuccess) this.attrs.onSuccess();
      }
    }).catch(() => {
      this.loading = false;
      m.redraw();
    });
  }
}
